<template>
  <v-app>
    <v-container>
      <Ph2GraphArea ref="chr" />
    </v-container>

    <div class="modal-mask" v-if="isLoading">
      <div style="align: center"></div>
      <div class="loading">
        <vue-loading
          type="spin"
          color="#ff0"
          :size="{ width: '50px', height: '50px' }"
        ></vue-loading>
      </div>
    </div>
    <wait-dialog ref="wait" />
  </v-app>
</template>
  
  <script>
import { SensorChart } from "@/lib/graph/ApexCharts/SensorChart.js";
import Ph2GraphArea from "./Ph2GraphArea.vue";
import { VueLoading } from "vue-loading-template";
import { useSensoreData } from "@/api/SensorDataAPI.js";

import WaitDialog from "@/components/dialog/WaitDialog.vue";

export default {
  data() {
    return {
      selectedItem: {},
      isLoading: false, // 

      yTitle: [
        { text: "()"},
        { text: "(RH)"},
        { text: "(W/)" },
        { text: "1(g/h)" },
        { text: "(m)" },
        { text: "(raw counts)" },
        { text: "(pF)" },
        { text: "()" },
        { text: "(mol/s)" },
        { text: "2(g/h)"},
      ],

      xTitle: [
        { text: ""},
      ],
    };
  },
  components: {
    Ph2GraphArea,
    VueLoading,
    WaitDialog,
  },
  methods: {
    //* --------------------------------------------
    //* 
    //* --------------------------------------------
    setGraphData: function (titlePaths, contentId, sensorId, startDate, endDate, interval, name, title) {
      this.isLoading = true;
      this.$nextTick(
        function () {
          this.setSonsorData(titlePaths, contentId, sensorId, startDate, endDate, interval, name, title);
        }.bind(this)
      );
    },
    setSonsorData(titlePaths, contentId, sensorId, startDate, endDate, interval, name, title) {
      this.$refs.wait.start("", false);
      useSensoreData(sensorId, startDate, endDate, interval)
        .then((response) => {
          // 
          const { status, message, data } = response["data"];
          if (status === 0) {
            // 
            let gc = new SensorChart();
            gc.setOptions(title, this.xTitle[0].text, this.yTitle[contentId-1].text, data);
            console.log(data.category);
            // 
            gc.setLoadingParent(this);
            this.$refs.chr.addGraph(
              titlePaths,
              gc.data.chartOptions,
              data.values,
              false,
              name
            );
          } else {
            throw new Error(message);
          }
        })
        .catch((error) => {
          //
          console.log(error);
        }).finally(()=>{
          this.$refs.wait.finish();
        });
    },
  },
};
</script>
  
